/* Write your CSS here */
.bg-container {
  background-color: white;
  height: 100vh;
  background-size: cover;
  text-align: center;
}

.dark-mode {
  background-color: black;
}

.container {
  margin: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
}

.white-mode {
  background-color: #ffffff;
  border-color: black;
  border-width: 2px;
  border-style: dotted;
}

.button {
  background-color: transparent;
  border-width: 2px;
  border-color: black;
}

.button-ele {
  width: 190px;
  height: 60px;
  border-radius: 8px;
  margin-bottom: 10px;
}

.white-head {
  color: white;
}

.dark-head {
  color: black;
}
